.rodape {
	margin-top: 42px;
	padding-top: 32px;
	background-color: var(--preto);
}

.rede_sociais {
	display: flex;
	justify-content: space-evenly;
	padding-inline: 1.5rem;
}

.redes_sociais_icons {
	height: 36px;
}

.titulo_sub {
	display: flex;
	flex-direction: column;
	gap: 12px;
	margin-top: 32px;
	padding-inline: 10vw;
}

.titulo_rodape {
	font-size: 1.3rem;
}

.titulo_rodape a {
	font-size: 1.3rem;
	font-weight: 600;
	font-family: var(--fonte-principal);
	color: var(--branco);
}

.titulo_academia_destaque_rodape,
.fit_descricao {
	font-weight: 500;
	font-family: var(--fonte-segundaria);
	background: var(--azul-gradiente);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.rodape_descricao {
	font-size: .9rem;
	font-weight: 300;
	font-family: var(--fonte-segundaria);
}

.num_email_local {
	display: flex;
	flex-direction: column;
	margin-top: 32px;
	padding-inline: 10vw;
	gap: 12px;
}

.num_email_local a {
	font-size: .9rem;
	font-weight: 300;
	font-family: var(--fonte-segundaria);
	color: var(--branco);
}

.rodape_icon {
	padding-right: 8px;
}

.telefone,
.e-mail,
.local {
	display: flex;
}

.rodape_mais_info {
	display: flex;
	flex-direction: column;
	gap: 16px;
	padding-inline: 10vw;
	line-height: 22px;
	margin-top: 32px;
}

.rodape_titulos {
	font-size: .9rem;
	background: var(--azul-gradiente);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.rodape_itens_info a {
	font-size: .9rem;
	color: var(--branco-f5);
}

.linha {
	border: none;
	height: 2px;
	margin-top: 12px;
	margin-bottom: 12px;
	background: var(--azul-gradiente);
}

.rodape_creditos {
	padding-inline: 10vw;
	font-weight: 300;
	color: var(--branco-f5);
	padding-bottom: 1px;
}

.creditos1 {
	display: flex;
	justify-content: center;
}

.creditos {
	display: flex;
	text-align: center;
	justify-content: space-evenly;
	margin: 22px 0;
}

@media screen and (min-width: 425px) {

	.rodape_descricao,
	.num_email_local a,
	.rodape_titulos,
	.rodape_itens_info a {
		font-size: 1rem;
	}

	.titulo_rodape,
	.titulo_rodape a {
		font-size: 1.4rem;
	}
}

@media screen and (min-width: 1024px) {

	.rodape_descricao,
	.num_email_local a,
	.rodape_titulos,
	.rodape_itens_info a {
		font-size: 1.1rem;
	}

	.titulo_rodape,
	.titulo_rodape a {
		font-size: 1.5rem;
	}

	.rodape_mais_info {
		line-height: 24px;
	}

	.rodape_icon {
		width: 18px;
	}
}

@media screen and (min-width: 1280px) {
	.redes_sociais_icons {
		height: 42px;
	}
}

@media screen and (min-width: 1440px) {

	.rodape_descricao,
	.num_email_local a,
	.rodape_titulos,
	.rodape_itens_info a {
		font-size: 1.2rem;
	}

	.titulo_rodape,
	.titulo_rodape a {
		font-size: 1.6rem;
	}

	.rodape_mais_info {
		line-height: 26px;
	}

	.rodape_icon {
		width: 20px;
	}
}